}
.movie_name{
  position: absolute;
  text-align: left;
  font-family: 'Rajdhani', sans-serif;
  margin-left: 7%;
  margin-top: -40px;
  font-weight: 400;
  font-size: 30px;
  color: rgb(255, 254, 251);
}
.movie_name_2{
  margin-bottom: -20px;
  margin-top: -10px;

}
@media screen and (max-width:340px) {
  .movie{
   display: none;
  }
