@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;1,200&display=swap");
.movie_container {
  margin: 0;
  display: flex;
}
.cards-list {
  z-index: 0;
  width: 75%;
  display: flex;
  margin-left: 35px;
  justify-content: space-around;
  flex-wrap: wrap;
  overflow-y: scroll;
  height: 88vh;
}
::-webkit-scrollbar {
  width: 0px;
}

.side_menu {
  /* display: flex; */
  /* border-radius: 20px; */
  overflow-y: scroll;
  height: 88vh;
  flex-direction: column;
  width: 20%;
  border-right: 2px solid rgba(73, 71, 71, 0.425);
  background-image: linear-gradient(75deg, #1f2b31 0%, #6dc2d7 100%);
}
.side_menu_logo {
  height: 40px;
  width: 200px;
  margin-left: 15px;
  margin-top: 10px;
}
.discover {
  justify-content: none;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  color: white;
  text-align: left;
  margin-left: 30px;
}
.discover h4 {
  font-weight: 500;
  color: aqua;
}
.side_menu_p {
  font-weight: 200;
  cursor: pointer;
  width: 120px;
  margin-left: 20px;
  padding: 10px;
  justify-content: center;
  /* display: flex; */
  align-items: center;
}
.side_menu_p:hover {
  border: 1px solid rgba(81, 198, 228, 0.822);
  border-radius: 15px;
}
.side_menu_p:active {
  border: 2px solid rgba(10, 90, 110, 0.822);
}

.card {
  overflow: hidden;
  margin: 30px 40px;
  width: 245px;
  height: 350px;
  font-size: 15px;
  color: rgba(58, 57, 57, 0.897);
  border-radius: 15px;
  cursor: pointer;
  transition: 1s;
}
.card:hover {
  background-color: rgba(9, 59, 43, 0.521);
  transition: all 400ms cursor(0.215, 0.61, 0.355, 1);
  color: white;
  width: 250px;
  height: 355px;
  box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px,
    rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px,
    rgba(0, 0, 0, 0.09) 0px 32px 16px;
}

.card .card_image {
  width: inherit;
  height: inherit;
  border-radius: 40px;
}

.card .card_image img {
  width: inherit;
  height: inherit;
  border-radius: 10px;
  box-shadow: rgba(3, 3, 31, 0.171) 0px 10px 7px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  transition: all 100ms cubic-bezier(0.075, 0.82, 0.165, 1);

  height: 300px;
  object-fit: fill;
}

.card .card_title {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-size: 17px;
  margin-top: -48px;
}

.card_year {
  margin-top: -15px;
}

.title-black {
  color: black;
}

@media all and (max-width: 500px) {
  .card-list {
    /* On small screens, we are no longer using row direction but column */
    flex-direction: column;
  }
}

@media screen and (max-width: 340px) {
  .movie_container {
    margin: 0;
    display: flex;
    flex-direction: column;
  }
  .side_menu {
    height: 15vh;
    flex-direction: column;
    overflow-x:scroll;
    
    border: none;
    width: 100%;
    border-right: none;
    background-image: linear-gradient(75deg, #1f2b31 0%, #6dc2d7 100%);
  }
  .side_menu_logo {
    height: 30px;
    width: 140px;
    margin-left: 15px;
    margin-top: 0px;
  }
  .discover {
    display: flex;
    flex-direction: row;
    justify-content: none;
    text-transform: uppercase;
    font-family: "Montserrat", sans-serif;
    color: white;
    text-align: left;
    margin-left: 10px;
    margin-top: -30px;
  }
  .discover h4 {
    display: none;
    font-weight: 500;
    color: aqua;
  }
  .side_menu_p {
    font-weight: 200;
    cursor: pointer;
    /* width: 120px; */
    margin-left: 20px;
    padding: 10px;
    justify-content: center;
    display: flex;
    align-items: center;
  }
  .side_menu_p:hover {
    border: none;
    border-radius:0px;
  }
  .cards-list {
    z-index: 0;
    width: 100%;
    display: flex;
    overflow: hidden;
    margin-left: 0px;
    justify-content: space-around;
    flex-wrap: wrap;
    overflow-y: scroll;
    /* height: 88vh; */
  }
  .card {
    overflow: hidden;
    margin: 10px auto;
    width: 147px;
    height: 200px;
    font-size: 15px;
    color: rgba(58, 57, 57, 0.897);
    border-radius: 15px;
    cursor: pointer;
    transition: 0.5s;
  }
  .card .card_image img {
    width: inherit;
    height: inherit;
    border-radius: 10px;
    box-shadow: rgba(3, 3, 31, 0.171) 0px 10px 7px -1px,
      rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    transition: all 100ms cubic-bezier(0.075, 0.82, 0.165, 1);
    height: 150px;
    object-fit: fill;
  }
  .card:hover {
    width: 150px;
    height: 210px;
  }
  .card .card_title {
    margin-top: -48px;
  }
  
  .card_year {
    margin-top: -15px;
  }
}
